import { Button, Radio, Select, Space } from "antd";
import React, { useState } from "react";
import concatIcon from "../../../Components/Icons/DataLake/concatIcon.svg";
import concatImage from "../../../Components/Icons/DataLake/concatImage.svg";
import "./styles.css";
import bucketIcon from "../../../Components/Icons/DataLake/bucketIcon.svg";
import { useParams } from "react-router-dom";
import datasetIcon from "../../../Components/Icons/DataLake/datasetIcon.svg";
import UploadCollapsable from "../../../Components/Collapsable/UploadCollapsable/UploadCollapsable";
import DataLakeConcatDropzone from "../../../Components/Dropzone/DataLakeConcatDropzone/DataLakeConcatDropzone";

export default function DataLakeConcat() {
  const { Option } = Select;

  const { databucket, dataset } = useParams();
  const [value, setValue] = React.useState(1);
  const [bucketname, setbucketname] = useState(null);
  const [datasetname, setdatasetname] = useState(null);
  const [option1, setoption1] = useState(1);
  const [option2, setoption2] = useState(1);

  function handleChange(value) {
    setbucketname(value);
  }
  function handleDatasetName(value) {
    setdatasetname(value);
  }
  const onChange = (e) => {
    setValue(e.target.value);
  };
  function ChangeOption1(e) {
    setoption1(e.target.value);
  }
  function ChangeOption2(e) {
    setoption2(e.target.value);
  }

  return (
    <div
      style={{ display: "flex", flexDirection: "column", height: "100%" }}
      className="DataLakeConcat"
    >
      <div style={{ textAlign: "left" }}>
        <img src={concatIcon} alt="icon" style={{ marginBottom: "5px" }} />{" "}
        <span
          style={{
            fontStyle: "normal",
            fontWeight: "bold",
            fontSize: "18px",
            marginLeft: "3px",
          }}
        >
          Concatenate
        </span>
      </div>
      <div style={{ flexGrow: "1", marginTop: "10px" }}>
        <div style={{ display: "flex" }}>
          <div className="card">
            <div className="title">Primary Dataset</div>
            <div style={{ display: "flex" }}>
              <img src={bucketIcon} alt="icon" width={22} />
              <div
                style={{
                  marginLeft: "10px",
                  color: "#6d6d6d",
                  fontSize: "14px",
                }}
              >
                {databucket}
              </div>
            </div>
            <img
              src={datasetIcon}
              alt="icon"
              width={56}
              style={{ marginTop: "15px" }}
            />
            <p
              style={{
                fontStyle: "normal",
                fontSize: "14px",
                color: "#6d6d6d",
                marginTop: "14px",
              }}
            >
              {dataset}
            </p>
          </div>
          <div>
            <img
              src={concatImage}
              alt="icon"
              style={{
                marginTop: "22vh",
                marginLeft: "20px",
                marginRight: "20px",
              }}
            />
          </div>
          <div className="card">
            <div className="title" style={{ marginBottom: "5px" }}>
              Secondary Dataset
            </div>
            <div
              style={{
                fontSize: "14px",
                fontStyle: "normal",
                textAlign: "left",
              }}
            >
              Concatenate Dataset
            </div>
            <div style={{ textAlign: "left" }}>
              <Radio.Group onChange={onChange} value={value}>
                <Radio
                  value={1}
                  style={{
                    marginRight: "80px",
                    fontSize: "14px",
                    color: "#6d6d6d",
                  }}
                >
                  Existing
                </Radio>
                <Radio
                  value={2}
                  style={{
                    fontSize: "14px",
                    color: "#6d6d6d",
                  }}
                >
                  From Local Storage
                </Radio>
              </Radio.Group>
            </div>
            {value === 1 ? (
              <div style={{ textAlign: "left", marginTop: "15px" }}>
                <div>Select </div>
                <Select
                  style={{
                    width: 200,
                    display: "inline-block",
                    marginRight: "15px",
                  }}
                  className="select"
                  onChange={handleChange}
                  placeholder="Databucket"
                  value={bucketname}
                >
                  <Option value="abc">Abc</Option>
                  <Option value="new">New Bucket</Option>
                  <Option value="Ohio">OHIO Bucket</Option>
                </Select>
                <Select
                  style={{ width: 200, display: "inline-block" }}
                  onChange={handleDatasetName}
                  placeholder="Dataset"
                  className="select"
                  value={datasetname}
                >
                  <Option value="abc">Abc</Option>
                  <Option value="new">New Dataset</Option>
                  <Option value="Ohio">OHIO Dataset</Option>
                </Select>
              </div>
            ) : (
              <div style={{ marginTop: "10px" }}>
                <DataLakeConcatDropzone />
              </div>
            )}
          </div>
        </div>
        <div className="card2">
          <div className="title" style={{ marginBottom: "8px" }}>
            Concatenated Dataset
          </div>
          {value === 1 ? (
            <div style={{ textAlign: "left" }}>
              <Radio.Group onChange={ChangeOption1} value={option1}>
                <Space direction="vertical">
                  <Radio
                    value={1}
                    style={{
                      marginRight: "80px",
                      fontSize: "14px",
                      color: "#6d6d6d",
                    }}
                  >
                    Concatenate Dataset{" "}
                    <span style={{ color: "#085fab" }}>'{dataset}'</span> and
                    Keep <span>{datasetname}</span>
                  </Radio>
                  <Radio
                    value={2}
                    style={{
                      fontSize: "14px",
                      color: "#6d6d6d",
                    }}
                  >
                    Concatenate Dataset{" "}
                    <span style={{ color: "#085fab" }}>'{dataset}'</span> and
                    Delete <span>{datasetname}</span>
                  </Radio>
                  <Radio
                    value={3}
                    style={{
                      fontSize: "14px",
                      color: "#6d6d6d",
                    }}
                  >
                    Create Dataset{" "}
                    <span style={{ color: "#085fab" }}>
                      '{dataset} Concatenated'
                    </span>{" "}
                    and Keep{" "}
                    <span style={{ color: "#085fab" }}>'{dataset}'</span> and
                    <span style={{ color: "#085fab" }}>'{datasetname}'</span>
                  </Radio>
                </Space>
              </Radio.Group>
            </div>
          ) : (
            <div style={{ textAlign: "left" }}>
              <Radio.Group onChange={ChangeOption2} value={option2}>
                <Space direction="vertical">
                  <Radio
                    value={1}
                    style={{
                      marginRight: "80px",
                      fontSize: "14px",
                      color: "#6d6d6d",
                    }}
                  >
                    Concatenate Dataset{" "}
                    <span style={{ color: "#085fab" }}>'{dataset}'</span>
                  </Radio>
                  <Radio
                    value={2}
                    style={{
                      fontSize: "14px",
                      color: "#6d6d6d",
                    }}
                  >
                    Concatenate Dataset{" "}
                    <span style={{ color: "#085fab" }}>'{dataset}'</span> and
                    Keep <span>{datasetname}</span>
                  </Radio>
                </Space>
              </Radio.Group>
            </div>
          )}
        </div>
      </div>
      <Button className="concatButton">Concatenate</Button>
      <div style={{ position: "fixed", bottom: "0", right: "0" }}>
        <UploadCollapsable />
      </div>
    </div>
  );
}
