import { Button } from "antd";
import React, { useState } from "react";
import { useParams } from "react-router-dom";
import Cliploader from "../../../Components/Loader/Cliploader";
import AutoMLCustomisedDatasetTabs from "../../../Components/Tabs/AutoMLCustomisedDatasetTabs/AutoMLCustomisedDatasetTabs";
import "./styles.css";
import InfoIcon from "../../../Components/Icons/AutoML/infopreview.svg";
import targetIcon from "../../../Components/Icons/AutoML/targeticon.svg";
import AutoMLCustomisedDatasetsPreviewTable from "../../../Components/Tables/AutoMLCustomisedDatasetsPreviewTable/AutoMLCustomisedDatasetsPreviewTable";
import DataLakeConfigureMetaTable from "../../../Components/Tables/DataLakeConfigureMetaTable/DataLakeConfigureMetaTable";

export default function DatasetConfigure() {
  let { dataset } = useParams();

  const [tab, settab] = useState("meta_data");
  const [target, settarget] = useState({ id: "", name: "Quantity" });
  const [data, setdata] = useState([
    {
      name: "Customer ID",
      id: 1,
      data_type: "Integer",
      nullable: "false",
      missing_values: "1.5%",
      invalid_values: "1.2%",
      distinct_values: "1022",
      correlation: "126",
      options: ["Integer", "String"],
    },
    {
      name: "Customer Name",
      id: 2,
      data_type: "String",
      nullable: "true",
      missing_values: "2.5%",
      invalid_values: "1.4%",
      distinct_values: "1452",
      correlation: "056",
      options: ["Integer", "String"],
    },
    {
      name: "Customer Address",
      id: 3,
      data_type: "String",
      nullable: "false",
      missing_values: "1.2%",
      invalid_values: "2.5%",
      distinct_values: "1342",
      correlation: "157",
      options: ["Integer", "String"],
    },
    {
      name: "Date of Purchase",
      id: 4,
      data_type: "String",
      nullable: "true",
      missing_values: "2.4%",
      invalid_values: "3.1%",
      distinct_values: "1410",
      correlation: "202",
      options: ["Integer", "String"],
    },
    {
      name: "Quantity",
      id: 5,
      data_type: "Integer",
      nullable: "true",
      missing_values: "2.2%",
      invalid_values: "0%",
      distinct_values: "1502",
      correlation: "111",
      options: ["Integer", "String"],
    },
  ]);
  const [loading, setloading] = useState(false);
  const [previewrows, setpreviewrows] = useState([
    [
      "Customer ID",
      "Product ID",
      "Dept Name",
      "Quantity",
      "Lorem ipsum",
      "Price",
    ],
    ["1234", "AB567", "QIB768", "Lorem Ipsum", "Oil Barrel", "1500"],
    ["1234", "AB567", "QIB768", "Lorem Ipsum", "Oil Barrel", "1500"],
    ["1234", "AB567", "QIB768", "Lorem Ipsum", "Oil Barrel", "1500"],
    ["1234", "AB567", "QIB768", "Lorem Ipsum", "Oil Barrel", "1500"],
    ["1234", "AB567", "QIB768", "Lorem Ipsum", "Oil Barrel", "1500"],
    ["1234", "AB567", "QIB768", "Lorem Ipsum", "Oil Barrel", "1500"],
    ["1234", "AB567", "QIB768", "Lorem Ipsum", "Oil Barrel", "1500"],
    ["1234", "AB567", "QIB768", "Lorem Ipsum", "Oil Barrel", "1500"],
    ["1234", "AB567", "QIB768", "Lorem Ipsum", "Oil Barrel", "1500"],
    ["1234", "AB567", "QIB768", "Lorem Ipsum", "Oil Barrel", "1500"],
  ]);
  const [rendertable, setrendertable] = useState(false);

  const changeType = (val, index) => {
    let temp = [];
    data.forEach((element) => {
      temp.push(element);
    });
    temp[index].data_type = val;
    setdata(temp);
    setrendertable(!rendertable);
  };

  return (
    <div className="DatasetConfigure">
      <h3
        style={{
          textAlign: "left",
          fontSize: "20px",
          fontWeight: "bold",
          marginBottom: "1px",
        }}
      >
        Configuration
      </h3>
      <h3 className="titleBold">{dataset}</h3>
      <hr
        style={{
          width: "100%",
          backgroundColor: "#E1EEFF",
          border: "none",
          height: "1px",
          marginBottom: "0px",
        }}
      />
      <div>
        <AutoMLCustomisedDatasetTabs setTab={(val) => settab(val)} />
      </div>

      <div
        className="box1"
        style={
          tab === "meta_data"
            ? { display: "flex", flexDirection: "column", height: "inherit" }
            : { display: "none" }
        }
      >
        <div style={{ textAlign: "left" }}>
          <h3 className="rowtitle">{dataset}</h3>
          <ul className="custom_row">
            {data.map((d) => (
              <li
                key={d.name}
                className={target.name === d.name ? "highlighted" : "listitem"}
                style={{ display: "inline-block" }}
              >
                <img
                  src={targetIcon}
                  alt="targetIcon"
                  width={13}
                  style={
                    target.name === d.name
                      ? { marginRight: "5px", paddingBottom: "1px" }
                      : { display: "none" }
                  }
                />
                {d.name}
              </li>
            ))}
          </ul>
          <hr
            style={{
              width: "100%",
              backgroundColor: "#E1EEFF",
              border: "none",
              height: "1px",
              marginTop: "20px",
              marginBottom: "10px",
            }}
          />
        </div>
        <div
          style={{
            flexGrow: "1",
            height: "10vh",
            width: "70%",
            marginBottom: "5px",
            overflowY: "scroll",
          }}
        >
          <DataLakeConfigureMetaTable
            data={data}
            render={rendertable}
            changeType={(val, index) => changeType(val, index)}
          />
        </div>
        <div style={{ display: "flex" }}>
          <div className="discard">Discard</div>
          <Button className="generatebutton">Save</Button>
        </div>
      </div>
      <div
        className="box2"
        style={
          tab === "meta_data"
            ? { display: "none" }
            : { display: "flex", flexDirection: "column", height: "inherit" }
        }
      >
        <div style={{ flexGrow: "1", overflowY: "scroll", height: "10vh" }}>
          <p style={{ textAlign: "left", color: "#6d6d6d", fontSize: "13px" }}>
            <img src={InfoIcon} alt="info" width={12} /> Showing data for first
            50 rows only
          </p>
          <AutoMLCustomisedDatasetsPreviewTable
            rows={previewrows}
            target={target.name}
          />
        </div>
        <hr
          style={{
            width: "100%",
            backgroundColor: "#E1EEFF",
            border: "none",
            height: "1px",
            marginBottom: "0px",
          }}
        />
        <Button className="generatebutton">Generate Correlation</Button>
      </div>
      <Cliploader loading={loading} />
    </div>
  );
}
